.form-control {
  border-collapse: collapse;
  border: .1rem solid #ced4da;
  box-shadow: 0 0 0 .1rem #0000;
}

.form-control:focus {
  border: .1rem solid #ced4da;
}

.form-container {
  width: 35%;
  height: auto;
  background-color: #fff;
  border-radius: 9px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2.4rem;
  transition: all .3s;
  display: flex;
  transform: scale(1.3);
  box-shadow: 0 1rem 2rem 2rem #7853531f;
}

.form {
  width: 100%;
  grid-template-columns: 1fr 1fr;
  gap: 1.2rem;
  font-size: 1.4rem;
  display: grid;
}

.width {
  width: 100%;
  grid-column: 1 / -1;
}

.form-control {
  border-top-right-radius: 9px !important;
  border-bottom-right-radius: 9px !important;
}

.input-group-text {
  font-size: 1.4rem;
  border-top-left-radius: 9px !important;
  border-bottom-left-radius: 9px !important;
}

.input-group {
  height: 25%;
}

.title {
  text-transform: uppercase;
  letter-spacing: .75px;
  grid-column: 1 / -1;
  margin-bottom: 1.8rem;
  font-size: 1.8rem;
  font-weight: 600;
}

.logo {
  width: 25%;
  margin-bottom: -1rem;
}

.btns {
  width: 100%;
  grid-column: 1 / -1;
  gap: 1.2rem;
  display: flex;
}

.btn-primary {
  width: 200%;
}

.btn-outline-primary {
  width: 100%;
}

